<template>
  <div class="maintodo">
    <input type="text" class="addtodo"  placeholder="what to do?     (回车添加)" autofocus 
 v-model="content"  @keyup.enter="addTodo" >

    <Todoitem v-for="(item,index) in filterData" :key="index" :todo='item' @del='handledeleteitem'></Todoitem>

    <Todoinfo :total="total" @toggleState="handleToggleState" @clearCompleted="handleClear"></Todoinfo>
  </div>
</template>

<script>
import Todoitem from './coms/Todoitem'
import Todoinfo from './coms/Todoinfo'

export default {
  name:'MainTodo',
  data(){
    return{
      tododata:[ ],
      content:'',
      total: 0,
      filter: 'all',
    }
  },
  methods: { 
    addTodo() {
      if (this.content === '') return

    this.tododata.unshift({ 
      id: 2,   
      content: this.content,   
      completed: false  
    })
    this.content = ''  
    },
    handledeleteitem(id){
      this.tododata.splice(this.tododata.findIndex(item => item.id === id),1)
    },
    handleToggleState(state) {
      this.filter = state 
    },
    handleClear() {
        this.tododata = this.tododata.filter(item => item.completed == false) 
    }
  },
  watch: {
   tododata: {
     deep: true,
     handler() {
       this.total = this.tododata.filter(item => item.completed == false).length
     }
   }
  },
  computed: {
   filterData() {
     switch (this.filter) {
       case 'all': 
         return this.tododata  
         break 
       case 'active':  
         return this.tododata.filter(item => item.completed == false) 
         break 
       case 'completed':
         return this.tododata.filter(item => item.completed == true)
         break  
    }  
   } 
  },
  components:{
    Todoitem,
    Todoinfo,
  }
}
</script>

<style>
.maintodo{
  margin: 0 auto;
  width: 600px;
  background-color: #fff;
  box-shadow: 0 0 5px #666; 
}

.addtodo{
  padding: 16px 16px 16px 36px;
  width: 100%;
  font-size: 24px;
  font-family: inherit;
  font-weight: inherit;
  color: inherit ;
  border: none;
  outline: none;
  box-sizing: border-box
}
</style>