<template>
  <div class="todo-info">
    <span class="total">{{total}} item left</span>
    <div class="tabs">
      <a v-for="(item,index) in states"
       :key="index"
        @click="toggleState(item)" 
        :class="['astate', state == item ? 'actived' : '']"
        >{{item}}</a>
    </div>
    <button class="clear" @click="clearCompleted">clear completed</button>
  </div>
</template>

<script>
export default {
  name:'Todoinfo',
  props:{
    total:Number
  },
  data(){
    return{
      states:['all','active','completed'],
      state:'all',
      
    }
  },
  methods:{
    toggleState(state){
      this.state =state,
      this.$emit('toggleState', state)
    },
    clearCompleted(){
      this.$emit('clearCompleted')
    }
  }
}
</script>

<style>
.todo-info{
  display: flex;
  justify-content: space-between;
  padding: 5px 10px;
  font-weight: 400;
  line-height: 30px;
  border-top: 1px solid rgba(0,0,0,.1);
}

.todo-info .total{
  color: rgb(254,67,101);
}

.tabs{
  display: flex;
  justify-content: space-between;
  width: 200px;
}

.tabs a{
  padding: 0 10px;
  border: 1px solid rgb(252, 157, 154);
  border-radius: 5px;
}

.tabs a.actived{
  background-color: rgb(252, 157, 154);
  color: #fff;
}

.clear{
  padding: 0 10px;
  background-color: rgb(131, 175, 155);
  border-radius: 5px;
  color: #fff;
  appearance: none;
  border: none;
  outline: none;
}
</style>