<template>
  <div :class="['todoitem', todo.completed ? 'completed':'']">
    <input type="checkbox" class="gougou" v-model="todo.completed"> 
    <label for="">{{todo.content}}</label>
    <button @click="delitem"></button>
  </div>
</template>

<script>
export default {
  name:'Todoitem',
  props:{
    todo:Object
  },
  methods:{
    delitem(){
      this.$emit('del',this.todo.id)
    }
  }
}
</script>

<style scoped>
.todoitem{
  display: flex;
  padding: 10px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  font-size: 24px;
  justify-content: space-between;
  border-top: 1px solid rgba(0, 0, 0, .1); 
}


  button:after{
    content:'X';
    color:rgb(172, 76, 92);
  }


 .todoitem .completed label{
  color: #d9d9d9;
  text-decoration: line-through;
  }

.gougou{
  width: 50px;
  height: 30px;
  appearance: none;
  border: none;
  outline: none;
}

.gougou::after{
  content:url('../../../assets/img/unChecked.svg');
}
.gougou:checked:after{
  content:url('../../../assets/img/checked.svg')
}

button{
  width: 40px;
  appearance: none;
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer
}

.todoitem.completed label{
  color: #d9d9d9;
  text-decoration: line-through;
}
</style>